.field-position {
  display: inline-block;
  background-color: lightblue;
  width: 40px;
  height: 40px;
  border: 1px solid black;
}

.field-position.missed {
  background-color: blue;
}

.field-position.injured {
  background-color: orange;
}

.field-position.killed {
  background-color: red;
}



